import React, { useEffect, useState } from 'react'
import { useLocation } from 'react-router-dom';
import Header from '../Header';
import Footer from '../Footer';
import styles from '../styles/Select_equip.module.css'


function Show_Distribution_Res() {
    
const IndivisualEquip = [
    "(11)()or(or)T(T)22()22()()()(1)()F1211",
    "(11)()or (or)T(T)22()()(1)()1211",
    "(11)()or (or)T(T)22()22()()()(1)()F1211",
    "(11)()or (or)T(T)22()()(1)()1211",
    "(11)()or(or)T(T)22()22()()()(1)()F()3801201211",
    "(11)()or (or)T(T)22()()(1)()()3801201211",
    "(11)()oror(or)T(T)22()22()()()(1)()F()1211",
    "(11)()or (or)T(T)22()()(1)()()1211",
    "(11)()oror(or)T(T)22()22()()()(1)()F()1211",
    "(11)()or (or)T(T)22()()(1)()()1211"
]
    const location = useLocation();
    const EquipList = location.state.EquipList
    const MemberNum = location.state.MemberSum
    const ClimbingType = location.state.ClimbingType
    const [MemberEquip, SetMemberEquip] = useState(new Array(MemberNum))
    MemberEquip.fill([])
    console.log(IndivisualEquip[ClimbingType])
    console.log(ClimbingType)
    useEffect(() => {
        EquipList.map((value) => {
            SetMemberEquip((prev) => {
                prev[value.MemberID] = [...prev[value.MemberID], value.Name]
                return prev
            })
        })
    })
    return (
        <>
            <div className={styles.Home}>
                <Header />
                <main className={styles.main}>
                    <p>
                        {IndivisualEquip[ClimbingType]}
                    </p>
                    {console.log("MemberEquip", MemberEquip)}
                    {
                        MemberEquip.map((value) => {
                            return (
                                <p>
                                    {value.map((val) => {
                                        return (
                                            <div>val</div>
                                        )
                                    })}
                                </p>
                            )
                        })
                    }
                </main>
                <Footer />
            </div>
        </>
    )
}

export default Show_Distribution_Res